@font-face { font-family: BTC; src: url(../assets/fonts/BTC.ttf); }
.btc {
  font-family: "BTC";
  line-height: initial;
}
.h3{
  font-size: 1.17em;
}
.h3 > *{
  display: inline-block;
  width: 15px;
}
.coinwidget{
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  margin: 0 .5rem .5rem;
}
.coinwidget-inner {
	padding: .5rem;
	/* margin: 0 .5rem .5rem; */
	background: #ddd;
	width: 100%;
	display: -ms-flexbox;
	display: flex;
  -ms-flex-align: start;
      align-items: flex-start;
}
.coinwidget-inner > img {
  width: 64px;
  margin: 0 1rem 0 0;
}
.coinwidget-inner > div {
  -ms-flex: 1 1;
      flex: 1 1;
}
.coinwidget-inner h2{
  font-weight: 500;
  margin: 0 0 0.5rem 0;
}
.coinwidget-inner h3 {
  font-weight: 500;
  margin: 0;
}

@media(min-width: 768px) {
  .coinwidget {
    width: 50%;
  }
}
@media(min-width: 968px) {
	.coinwidget {
	  width: 33.333333%;
	}
}
@media(min-width: 1200px) {
	.coinwidget {
	  width: 25%;
	}
}


.coinwidget-fade-enter {
  opacity: 0.01;
}

.coinwidget-fade-enter.coinwidget-fade-enter-active {
  opacity: 1;
  -webkit-transition: opacity 2000ms ease-in;
  -o-transition: opacity 2000ms ease-in;
  transition: opacity 2000ms ease-in;
}
.coinwidget-fade-exit {
  opacity: 1;
}

.coinwidget-fade-exit.coinwidget-fade-exit-active {
  opacity: 0.01;
  -webkit-transition: opacity 300ms ease-in;
  -o-transition: opacity 300ms ease-in;
  transition: opacity 300ms ease-in;
}



